import React, { useEffect, useState } from "react";

export default function GuessNumber() {
  const [range, setrange] = useState("0-5");
  const [isTrue, setisTrue] = useState(true);
  const [predictedAnswer, setpredictedAnswer] = useState();
  const [userAnswer, setuserAnswer] = useState();
  const [chance, setchance] = useState(3);
  const [levelNo, setlevelNo] = useState(1);
  const [level, setlevel] = useState("low");
  const [randomNumber, setrandomNumber] = useState({
    num1: 0,
    num2: 0,
    op: "+",
  });

  const isEnter = (e) => {
    if (e.which === 13) {
      const answer = parseInt(userAnswer);
      if (answer == predictedAnswer) {
        if (level == "low") {
          setlevelNo(levelNo + 1);
          setlevel("medium");
          setrange("10-20");
          setuserAnswer("");
          getRandomNumber();
        } else if (level == "medium") {
          setlevelNo(levelNo + 1);
          setlevel("high");
          setrange("20-60");
          setuserAnswer("");
          getRandomNumber();
        } else {
          setuserAnswer("");
          getRandomNumber();
        }
      } else {
        setuserAnswer("");
        setchance(chance - 1);
      }
    }
  };
  const getRandomNumber = () => {
    console.log(levelNo);
    if (levelNo == 1) {
      // console.log("aaa");
      var num1 = Math.floor(Math.random() * 5 + 1);
      var num2 = Math.floor(Math.random() * 5 + 1);
      const operation = ["+", "*", "-"];
      const random = Math.floor(Math.random() * operation.length);
      var operationValue = operation[random];
      setrandomNumber({ num1: num1, num2: num2, op: operationValue });
      const val = eval(`${num1}${operationValue}${num2}`);
      console.log(val);
      setpredictedAnswer(val);
    } else if (level == "medium") {
      var num1 = Math.floor(Math.random() * 20 + 10);
      var num2 = Math.floor(Math.random() * 20 + 10);
      const operation = ["+", "*", "-"];
      const random = Math.floor(Math.random() * operation.length);
      var operationValue = operation[random];
      setrandomNumber({ num1: num1, num2: num2, op: operationValue });
      const val = eval(`${num1}${operationValue}${num2}`);
      console.log(val);
      setpredictedAnswer(val);
    } else {
      var num1 = Math.floor(Math.random() * 60 + 20);
      var num2 = Math.floor(Math.random() * 60 + 20);
      const operation = ["+", "*", "-"];
      const random = Math.floor(Math.random() * operation.length);
      var operationValue = operation[random];
      setrandomNumber({ num1: num1, num2: num2, op: operationValue });
      const val = eval(`${num1}${operationValue}${num2}`);
      console.log(val);
      setpredictedAnswer(val);
    }
  };

  useEffect(() => {
    getRandomNumber();
    if (chance == 0) {
      setisTrue(false);
    }
  }, [chance]);
  const refreshPage = () => {
    setisTrue(true);
    setlevel("low");
    setlevelNo(1);
    setchance(3);
    setrange("0-5");
    getRandomNumber();
  };
  return (
    <div className="container">
      <div className="row">
        <div className="col-md-6 offset-md-3">
          <div
            className={
              level == "low"
                ? `card alert-success mt-5`
                : level == "medium"
                ? `card alert-warning mt-5`
                : `card alert-danger mt-5`
            }
          >
            <div className="card-header text-center">
              <img src="./m.gif" alt="" className="img-fluid h-25 w-25" />
              <h1>Guess The Number</h1>
            </div>

            {isTrue ? (
              <div className="card-body">
                <div className="d-flex justify-content-between">
                  <div className="text-center">
                    <h5>Range</h5>
                    <div>{range}</div>
                  </div>
                  <div className="text-center">
                    <h5>Level</h5>
                    <div>{level}</div>
                  </div>
                </div>
                <div className="d-flex justify-content-between mt-3">
                  <div className="text-center">
                    <h5>Level No</h5>
                    <div>{levelNo}</div>
                  </div>
                  <div className="text-center">
                    <h5>Chance</h5>
                    <div>{chance}</div>
                  </div>
                </div>
                <div className="mt-3 fs-1 text-center">
                  <span>{randomNumber.num1}</span>
                  <span>{randomNumber.op}</span>
                  <span>{randomNumber.num2}</span>
                </div>
                <input
                  type="text"
                  className="form-control mt-3 text-center"
                  placeholder="Enter your guess value"
                  value={userAnswer}
                  onChange={(e) => setuserAnswer(e.target.value)}
                  onKeyPress={isEnter}
                />
                <div className="mt-3 text-center">
                  Enter the <span className="fw-bold">Enter</span> button to
                  select the value
                </div>
              </div>
            ) : (
              <div className="card-body text-center">
                <h1 className="mt-3 ">Game Over</h1>
                <button onClick={refreshPage} className="btn btn-danger mt-3 ">
                  Play More
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
